import "../css/Guide.scss";
import { Link } from "react-router-dom";

const CONTENT = [
  // {
  //   id: 1,
  //   title: " ",
  //   dec: "  ",
  //   link: "/sub01",
  // },
  {
    id: 1,
    title: " ",
    dec: "    .",
    link: "/sub02",
    click: "  ",
  },
  {
    id: 2,
    title: " ",
    dec: "      .",
    link: "/list",
    click: " ",
  },
  {
    id: 3,
    title: " ",
    dec: "    .",
    link: "/cart",
    click: " ",
  },
];

const Guide = () => {
  return (
    <section className="Guide csc">
      <h2> </h2>
      <p>  </p>
      <div className="inner">
        {CONTENT.map((content) => {
          return (
            <Link to={content.link}>
              <figure key={content.id}>
                <div className="box">
                  <img
                    src={
                      process.env.PUBLIC_URL +
                      "/assets/images/main_solution_icon_0" +
                      content.id +
                      ".png"
                    }
                    alt=""
                  />
                </div>
                <div className="tit">{content.title}</div>
                <div className="dec">{content.dec}</div>
                <a href={content.link} className="cbtn">
                  {content.click}
                </a>
              </figure>
            </Link>
          );
        })}
      </div>
    </section>
  );
};
export default Guide;
