import "../css/News.scss";

const NEWS = [
  {
    id: 1,
    category: " ",
    title: "   ",
    date: "2022. 12. 14",
    link: "https://blog.sinsiway.com/",
    src: "/assets/images/main_news01.jpg",
  },
  {
    id: 2,
    category: "  ",
    title: "12 4  ",
    date: "2022. 12. 26",
    link: "https://blog.sinsiway.com/",
    src: "/assets/images/main_news02.jpg",
  },
  {
    id: 3,
    category: "",
    title: "   ",
    date: "2022. 12. 26",
    link: "https://blog.sinsiway.com/",
    src: "/assets/images/main_news03.jpg",
  },
];

const News = () => {
  return (
    <section className="News csc">
      <h2> </h2>
      <p>   .</p>
      <div className="inner">
        {NEWS.map((news) => {
          return (
            <figure key={news.id}>
              <a href={news.link}>
                <div className="box">
                  <img
                    src={process.env.PUBLIC_URL + news.src}
                    alt={news.title}
                  />
                </div>
                <strong>{news.category}</strong>
                <div className="title">{news.title}</div>
                <span>{news.date}</span>
              </a>
            </figure>
          );
        })}
      </div>
    </section>
  );
};
export default News;
